import gameContext from '../../../gameContext';
import { useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import socketService from '../../../services/socketService';
import toolkitService from '../../../services/toolkitService';
import '../Toolkit.css';
export function ToolkitAfter() {
  const {
    isProtagonist,
    isRoomName,
    textarea1,
    setTextarea1,
    textarea2,
    setTextarea2,
  } = useContext(gameContext);

  const [rendering, setRendring] = useState<boolean>(true);
  const [str, setStr] = useState('');
  const [str1, setStr1] = useState('');
  const history = useNavigate();

  let t1: any[] = [];
  let t2: any[] = [];

  // Onchange 함수
  const handleOnchange = (e: React.ChangeEvent<any>) => {
    setRendring(false);
    const value = e.target.value;
    switch (e.target.id) {
      case 'textarea1':
        setStr(value);
        break;
      case 'textarea2':
        setStr1(value);
        break;
      default:
        break;
    }
  };

  // 사후툴킷 내용 서버에 전송
  const onChangeWrite = (id: string, contents: string) => {
    if (isProtagonist) {
      if (socketService.socket) {
        toolkitService.onChangeWrite(socketService.socket, id, contents);
        setRendring(true);
        setStr('');
        setStr1('');
      }
    }
  };

  // 사후툴킷 내용 서버에서 받아오기

  // 툴킷 완료
  const finishToolkit = () => {
    if (socketService.socket)
      toolkitService.finishToolkit(socketService.socket, {
        roomName: isRoomName,
        before: false,
        text2: textarea1,
        text3: textarea2,
      });
    history('/');
  };

  // 사후 툴킷 내용 출력함수
  const display = (array: Array<string>) => {
    if (array.length === 0) return;
    else {
      switch (array) {
        case textarea1:
          for (var i = 0; i < array.length; i++) {
            t1.push(
              <>
                {array[i]}
                <br />
              </>
            );
          }
          return t1;
        case textarea2:
          for (var j = 0; j < array.length; j++) {
            t2.push(
              <>
                {array[j]}
                <br />
              </>
            );
          }
          return t2;
        default:
          break;
      }
    }
  };

  // 초마다 변화되는 내용 refresh
  useEffect(() => {
    const check = () => {
      if (socketService.socket) {
        toolkitService.updateWrite(socketService.socket, (id, contents) => {
          switch (id) {
            case 'textarea1':
              setTextarea1(textarea1.concat(contents));
              console.log(textarea1);
              break;
            case 'textarea2':
              setTextarea2(textarea2.concat(contents));
              break;
            default:
              break;
          }
        });
      }
    };
    if (rendering) {
      check();
    }
  });

  return (
    <div className="ToolkitAfterpage">
      <div>
        <div className="ToolkitAfterpageText">
          Q. 플랜빌리지 게임 진행 시, 나만의 전략이 있었나요?
        </div>
        <div style={{ display: 'flex' }}>
          <input
            className="ToolkitpageInput2"
            id="textarea1"
            value={str}
            type="text"
            onChange={handleOnchange}
          />

          <div
            className="Toolkitpagebtn"
            style={{ margin: '0 20px' }}
            onClick={() => onChangeWrite('textarea1', str)}
          />
        </div>

        <div className="ToolkitpageTextArea1">{display(textarea1)}</div>
      </div>

      <div>
        <div className="ToolkitAfterpageText">
          Q. 게임진행 시, 어려웠던 점은 무엇이었나요?
        </div>
        <div style={{ display: 'flex' }}>
          <input
            className="ToolkitpageInput2"
            id="textarea2"
            value={str1}
            type="text"
            onChange={handleOnchange}
          />
          <div
            className="Toolkitpagebtn"
            style={{ margin: '0 20px' }}
            onClick={() => onChangeWrite('textarea2', str1)}
          />
        </div>
        <div className="ToolkitpageTextArea1">{display(textarea2)}</div>
      </div>

      {textarea1.length > 3 && textarea2.length > 3 && (
        <div className="Toolkitpagehomebtn" onClick={() => finishToolkit()} />
      )}
    </div>
  );
}
