import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import gameContext from '../../gameContext';
import '../game/Game.css';
import A from '../../Img/cardA.png';
import C from '../../Img/cardC.png';
import H from '../../Img/cardH.png';
import N from '../../Img/cardN.png';
import V from '../../Img/cardV.png';
import S from '../../Img/cardS.png';
import Clear from '../../Img/cardClear.png';
import Swal from 'sweetalert2';

import {
  show,
  showCoin,
  displayColor,
  disasterType,
  impactavailty,
} from '../../services/display';
export function Tutorial() {
  const [isTCoin, setTCoin] = useState([
    { name: '리더십', num: 3 },
    { name: '창의력', num: 3 },
    { name: '문제해결', num: 3 },
    { name: '분석력', num: 3 },
    { name: '의사소통', num: 3 },
    { name: '직업윤리', num: 3 },
  ]);
  const resetTcoin = [
    { name: '리더십', num: 3 },
    { name: '창의력', num: 3 },
    { name: '문제해결', num: 3 },
    { name: '분석력', num: 3 },
    { name: '의사소통', num: 3 },
    { name: '직업윤리', num: 3 },
  ];
  const isTWin = [
    { name: '예술/디자인', num: 1 },
    { name: '사회공공기관', num: 2 },
    { name: '과학/IT/공학', num: 1 },
    { name: '의료/보건', num: 2 },
    { name: '서비스/봉사', num: 3 },
    { name: '자연친화', num: 1 },
  ];
  const [isTCollection, setTCollection] = useState([
    { name: '예술/디자인', num: 0 },
    { name: '사회공공기관', num: 2 },
    { name: '과학/IT/공학', num: 0 },
    { name: '의료/보건', num: 2 },
    { name: '서비스/봉사', num: 3 },
    { name: '자연친화', num: 1 },
  ]);
  const history = useNavigate();
  const [isTAct, setTAct] = useState<boolean>(false);
  const [isTFirstTurn, setTFirstTurn] = useState<boolean>(true);
  const [isTAvailtyTurn, setTAvailtyTurn] = useState<boolean>(false);
  const [isTBuyTurn, setTBuyTurn] = useState<boolean>(false);
  const [isTStateTurn, setTStateTurn] = useState<boolean>(false);
  const [isTEndingTurn, setTEndingTurn] = useState<boolean>(false);
  const [isTPhase, setTPhase] = useState<number>(0);
  const [isBuy, setBuy] = useState('');
  const { isUserName } = useContext(gameContext);

  const displaycard = () => {
    let m = [
      {
        name: '예술/디자인',
        num: 0,
        src: A,
        coin: [
          { name: '리더십', num: 0 },
          { name: '창의력', num: 5 },
          { name: '문제해결', num: 0 },
          { name: '분석력', num: 3 },
          { name: '의사소통', num: 2 },
          { name: '직업윤리', num: 0 },
        ],
      },
      {
        name: '사회공공기관',
        num: 0,
        src: C,
        coin: [
          { name: '리더십', num: 4 },
          { name: '창의력', num: 0 },
          { name: '문제해결', num: 0 },
          { name: '분석력', num: 0 },
          { name: '의사소통', num: 3 },
          { name: '직업윤리', num: 3 },
        ],
      },
      {
        name: '과학/IT/공학',
        num: 0,
        src: S,
        coin: [
          { name: '리더십', num: 2 },
          { name: '창의력', num: 0 },
          { name: '문제해결', num: 3 },
          { name: '분석력', num: 5 },
          { name: '의사소통', num: 0 },
          { name: '직업윤리', num: 0 },
        ],
      },
      {
        name: '의료/보건',
        num: 0,
        src: H,
        coin: [
          { name: '리더십', num: 0 },
          { name: '창의력', num: 0 },
          { name: '문제해결', num: 2 },
          { name: '분석력', num: 3 },
          { name: '의사소통', num: 0 },
          { name: '직업윤리', num: 5 },
        ],
      },
      {
        name: '서비스/봉사',
        num: 0,
        src: V,
        coin: [
          { name: '리더십', num: 0 },
          { name: '창의력', num: 0 },
          { name: '문제해결', num: 3 },
          { name: '분석력', num: 0 },
          { name: '의사소통', num: 3 },
          { name: '직업윤리', num: 4 },
        ],
      },
      {
        name: '자연친화',
        num: 0,
        src: N,
        coin: [
          { name: '리더십', num: 0 },
          { name: '창의력', num: 3 },
          { name: '문제해결', num: 3 },
          { name: '분석력', num: 4 },
          { name: '의사소통', num: 0 },
          { name: '직업윤리', num: 0 },
        ],
      },
    ];
    for (let i = 0; i < isTCollection.length; i++) {
      if (isTCollection[i].name === m[i].name) {
        m[i].num = isTCollection[i].num;
      }
    }

    return (
      <>
        {m.map((data) => {
          return (
            <div>
              {isTBuyTurn
                ? isTWin.map((item) => {
                    if (item.name === data.name && data.num >= item.num)
                      return (
                        <img
                          src={Clear}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ zIndex: 1 }}
                        />
                      );
                    else if (
                      (item.name === data.name &&
                        data.num <= item.num &&
                        item.name === '예술/디자인' &&
                        isTPhase === 1) ||
                      (item.name === data.name &&
                        data.num <= item.num &&
                        item.name === 'IT/과학' &&
                        isTPhase === 2)
                    )
                      return (
                        <img
                          className="hearbeatEffect"
                          src={data.src}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ zIndex: 1 }}
                          onClick={() => buyCard(data.name, data.coin)}
                        />
                      );
                    else if (item.name === data.name && data.num <= item.num)
                      return (
                        <img
                          src={data.src}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ zIndex: 1 }}
                          onClick={() => buyCard(data.name, data.coin)}
                        />
                      );

                    return '';
                  })
                : isTWin.map((item) => {
                    if (item.name === data.name && data.num >= item.num)
                      return (
                        <img
                          src={Clear}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ zIndex: 1 }}
                        />
                      );
                    else if (item.name === data.name && data.num <= item.num)
                      return (
                        <img
                          src={data.src}
                          alt=""
                          width="100%"
                          height="100%"
                          style={{ zIndex: 1 }}
                        />
                      );
                    return '';
                  })}
              <div style={{ position: 'relative', zIndex: 2, top: '-18%' }}>
                <div className="CardText_content">
                  <div>
                    {isTWin.map((item) => {
                      if (item.name === data.name && data.num < item.num)
                        return (
                          <label>
                            획득 카드 수 : {data.num} / {item.num}
                          </label>
                        );
                      if (item.name === data.name && data.num >= item.num)
                        return '';
                      return '';
                    })}
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </>
    );
  };
  const takeCoin = (name: string, num: number, calculate: string) => {
    let coinNum: number = num;
    if (isTFirstTurn) {
      if (calculate === '+') {
        coinNum = num + 1;
        const newPlayerCoin = isTCoin.map((item) =>
          item.num !== coinNum && item.name === name
            ? { name: item.name, num: coinNum }
            : item
        );
        setTCoin(newPlayerCoin);
        let allCoin: number = 1;
        isTCoin.map((item) => {
          return (allCoin = item.num + allCoin);
        });

        if (allCoin > 20) {
          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: '코인을 2개만 가져갈수 있습니다!',
          });
          setTCoin(resetTcoin);
          setTAct(false);
        } else if (allCoin === 20) {
          setTAct(true);
        }
      }
    }
  };
  const EndTurn = (when: string) => {
    let allCoin: number = 0;
    for (let i = 0; i < isTCoin.length; i++) {
      allCoin = allCoin + isTCoin[i].num;
    }
    // 첫 턴 종료
    if (when === 'End_BuyFirstCoin' && isTPhase === 0) {
      if (allCoin === 20) {
        Swal.fire({
          title: '<strong>코인구매 튜토리얼 종료</strong>',
          icon: 'info',
          html: '다음은 <strong style={{ color: "red" }}>"카드 구매 방법"</strong>에 대해 알아봅시다.',
        });
        setTCoin(resetTcoin);
        setTAct(false);
        setTPhase(1);
      } else {
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: '코인 2개를 선택하지 않았습니다.',
        });
        setTCoin(resetTcoin);
      }
    } else if (when === 'End_BuyFirstCoin' && isTPhase === 1) {
      if (allCoin === 20 && isTCoin[1].num === 5) {
        setTFirstTurn(false);
        setTAvailtyTurn(true);
        setTAct(false);
      } else {
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: '코인을 올바르게 가져오지 못했어요!',
        });
        setTCoin(resetTcoin);
        setTAct(false);
      }
    } else if (when === 'End_BuyFirstCoin' && isTPhase === 2) {
      if (allCoin === 20 && isTCoin[3].num === 5) {
        setTFirstTurn(false);
        setTAvailtyTurn(true);
        setTAct(false);
        disasterType(2, 3);
      } else {
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: '코인을 올바르게 가져오지 못했어요!',
        });
        setTCoin(resetTcoin);
        setTAct(false);
      }
    }
    // State 턴 종료
    else if (when === 'End_StateTurn' && isTPhase === 1) {
      Swal.fire({
        title: '<strong>카드구매 튜토리얼 종료</strong>',
        icon: 'info',
        html: '다음은 <strong style={{ color: "red" }}>"코인 능력 사용 방법"</strong>에 대해 알아봅시다.',
      });
      setTFirstTurn(true);
      setTStateTurn(false);
      setTPhase(2);
      setBuy('');
      setTCoin(resetTcoin);
    } else if (when === 'End_StateTurn' && isTPhase === 2) {
      Swal.fire({
        title: '<strong>튜토리얼 종료</strong>',
        icon: 'info',
      });
      setTEndingTurn(true);
    } else {
      return '';
    }
  };
  const FirstTurn = () => {
    return (
      <>
        <div style={{ display: 'inline-flex', paddingTop: '30px' }}>
          <div className="Cardbox">{displaycard()}</div>
          <div className="Alert">
            <div className="Round">Tutorial Mode</div>
            {isTPhase === 0 ? (
              <div className="AlertContent">
                <strong style={{ color: '#fcd303' }}>
                  "코인 구매 튜토리얼"
                </strong>
                <br />
                코인박스 위 + 버튼으로 원하는 코인을 2개 선택해주세요. 2개를
                모두 선택했으면 확인을 눌러주세요.
              </div>
            ) : isTPhase === 1 ? (
              <div className="AlertContent">
                <strong style={{ color: '#fcd303' }}>
                  "카드 구매 튜토리얼"
                </strong>
                <br />
                <strong style={{ color: 'red' }}>"예술디자인"</strong> 카드를
                구매하기 위해 창의력코인 2개를 구매해봅시다!
              </div>
            ) : (
              <div className="AlertContent">
                <strong style={{ color: '#fcd303' }}>
                  "코인능력 사용 튜토리얼"
                </strong>
                <br />
                <strong style={{ color: 'red' }}>"IT/과학"</strong>
                카드를 구매하기 위해 분석력코인 2개를 구매해봅시다!
              </div>
            )}
          </div>
        </div>
        <div className="Coinbox">
          {isTCoin.map((data: any) => {
            return (
              <div className="CoinContent">
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    marginTop: '10px',
                  }}
                >
                  {!isTAct && isTPhase === 0 ? (
                    <button
                      className="CoinAddButtonT"
                      onClick={() => takeCoin(data.name, data.num, '+')}
                    >
                      +
                    </button>
                  ) : (!isTAct && isTPhase === 1 && data.name === '창의력') ||
                    (!isTAct && isTPhase === 2 && data.name === '분석력') ? (
                    <button
                      className="CoinAddButtonT"
                      onClick={() => takeCoin(data.name, data.num, '+')}
                    >
                      +
                    </button>
                  ) : (
                    <button
                      className="CoinAddButton"
                      onClick={() => takeCoin(data.name, data.num, '+')}
                    >
                      +
                    </button>
                  )}

                  <button
                    className="CoinInfoButton"
                    onClick={() => show(data.name)}
                  >
                    ?
                  </button>
                </div>

                <div className="CoinImg">{showCoin(data.name)}</div>
                <div
                  className="CoinText"
                  style={{ color: displayColor(data.name) }}
                >
                  {data.name}
                </div>
                <div className="CoinNum">{data.num}</div>
              </div>
            );
          })}
          <div className="ControllBox">
            <button
              className="ControllButton"
              onClick={() => setTCoin(resetTcoin)}
            >
              다시고르기
            </button>
            {isTAct ? (
              <button
                className="ControllButtonT"
                onClick={() => EndTurn('End_BuyFirstCoin')}
              >
                확 인
              </button>
            ) : (
              <button
                className="ControllButton"
                onClick={() => EndTurn('End_BuyFirstCoin')}
              >
                확 인
              </button>
            )}
          </div>
        </div>
      </>
    );
  };
  const Availty = (data: string, num: number) => {
    return Swal.fire({
      toast: true,
      title: `<h1>능력 사용!</h1> `,
      html: `<h2>직업윤리 코인의 능력을 사용하시겠습니까? </h2><br/>
            능력 : 모범시민 <br/>
            시민의식 저하’의 효과(무작위 한 종류 코인을 전부 회수)를 막습니다.`,
      width: 500,
      showConfirmButton: true,
      showDenyButton: true,
      allowOutsideClick: false,
      denyButtonText: '취소',
      confirmButtonText: '확인',
      background: '#fff',
    }).then((result) => {
      if (result.isConfirmed) {
        const newPlayerCoin = isTCoin.map((item) =>
          item.name === data ? { name: item.name, num: item.num - 3 } : item
        );
        setTCoin(newPlayerCoin);
        setTAvailtyTurn(false);
        setTBuyTurn(true);
      }
    });
  };

  const NextPhase = () => {
    setTAvailtyTurn(false);
    setTBuyTurn(true);
  };

  const AvailtyPhase = () => {
    return (
      <>
        <div style={{ display: 'inline-flex', paddingTop: '30px' }}>
          <div className="Cardbox">{displaycard()}</div>
          <div className="Alert">
            <div className="Round">Tutorial Mode</div>
            <div className="AlertContent">
              {isTPhase === 1 ? (
                <>
                  <strong style={{ color: '#fcd303' }}>
                    "카드 구매 튜토리얼"
                  </strong>
                  <br />
                  사용하지 않기를 눌러 구매 단게로 넘어가세요!
                </>
              ) : (
                <>
                  <div onClick={() => disasterType(2, 3)} className="Warning" />
                  장애물이 도착했습니다. 사이렌을 눌러 장애물을 확인하고
                  <strong style={{ color: 'red' }}> 직업윤리 </strong>
                  코인으로 장애물을 막아봅시다!
                </>
              )}
            </div>
          </div>
        </div>
        {console.log(isTCoin)}
        <div className="Coinbox">
          {isTCoin.map((data) => {
            return (
              <div className="CoinContent">
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    marginTop: '10px',
                  }}
                >
                  <button
                    className="CoinAddButton"
                    onClick={() => show(data.name)}
                  >
                    ?
                  </button>
                </div>
                {data.name === '직업윤리' && isTPhase === 2 ? (
                  <div
                    className="CoinImgT"
                    onClick={() => Availty(data.name, data.num)}
                  >
                    {showCoin(data.name)}
                  </div>
                ) : (
                  <div className="CoinImg">{showCoin(data.name)}</div>
                )}
                <div
                  className="CoinText"
                  style={{ color: displayColor(data.name) }}
                >
                  {data.name}
                </div>
                <div className="CoinNum">{data.num}</div>
              </div>
            );
          })}
          {isTPhase === 1 ? (
            <div className="ControllBox">
              <button className="ControllButtonT" onClick={NextPhase}>
                사용하지 않기
              </button>
            </div>
          ) : (
            <div className="ControllBox">
              <button
                className="ControllButton"
                onClick={() => {
                  Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: '장애물을 막아야만 해요!',
                  });
                }}
              >
                사용하지 않기
              </button>
            </div>
          )}
        </div>
      </>
    );
  };
  const buyCard = (name: string, coin: any) => {
    Swal.fire({
      title: '전략카드 구매',
      text: name + ' 카드를 구매하시겠습니까?',
      showCancelButton: true,
      confirmButtonText: '예',
      cancelButtonText: '아니오',
    }).then((result) => {
      if (result.isConfirmed) {
        const myCoin: number[] = isTCoin.map((data) => {
          return data.num;
        });

        let CardPrice: number[];
        CardPrice = coin.map((data: any) => {
          return data.num;
        });

        const result: number[] = [];

        for (let i = 0; i < myCoin.length; i++) {
          result[i] = myCoin[i] - CardPrice[i];
        }

        if (!result.some((data) => data < 0)) {
          const newPlayerCoin = isTCoin.map((item, index) =>
            item.num !== result[index]
              ? { name: item.name, num: result[index] }
              : item
          );
          setTCoin(newPlayerCoin);
          if (isTPhase === 1) {
            setTCollection([
              { name: '예술/디자인', num: 1 },
              { name: '사회공공기관', num: 2 },
              { name: '과학/IT/공학', num: 0 },
              { name: '의료/보건', num: 2 },
              { name: '서비스/봉사', num: 3 },
              { name: '자연친화', num: 1 },
            ]);
            setTBuyTurn(false);
            setTStateTurn(true);
          } else {
            setTCollection([
              { name: '예술/디자인', num: 1 },
              { name: '사회공공기관', num: 2 },
              { name: '과학/IT/공학', num: 1 },
              { name: '의료/보건', num: 2 },
              { name: '서비스/봉사', num: 3 },
              { name: '자연친화', num: 1 },
            ]);
            setTBuyTurn(false);
            setTStateTurn(true);
          }
        } else {
          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: '필요한 코인이 부족합니다.',
          });
        }
      } else if (result.isDenied) {
        Swal.fire('구매를 취소하였습니다.', '', 'error');
      }
    });
  };
  const BuyPhase = () => {
    return (
      <>
        <div style={{ display: 'inline-flex', paddingTop: '30px' }}>
          {isBuy === '' ? (
            <div className="Cardbox">
              <div className="AvailtyButton_content"></div>

              <div className="AvailtyButton_content"></div>

              <div className="AvailtyButton_content"></div>

              <div className="AvailtyButton_content"></div>

              <div className="AvailtyButton_content"></div>
              <div className="AvailtyButton_content">
                {isTPhase === 2 ? (
                  <div>
                    <>
                      <div
                        className="AvailtyButton_effect"
                        onClick={() => impactavailty('직업윤리')}
                      >
                        {showCoin('직업윤리')}
                      </div>
                      <div className="AvailtyButton_text">능력 발동!</div>
                    </>
                  </div>
                ) : (
                  ''
                )}
              </div>
            </div>
          ) : isBuy === 'Card' ? (
            <div className="Cardbox">{displaycard()}</div>
          ) : (
            ''
          )}
          <div className="Alert">
            <div className="Round">Tutorial Mode</div>
            <div className="AlertContent">
              {isBuy === '' ? (
                isTPhase === 2 ? (
                  <>
                    발동된 코인의 능력을 확인 할 수 있어요!
                    <strong style={{ color: 'red' }}> 직업윤리 </strong> 코인
                    능력으로 판단력 코인이 회수되지 않았어요!
                    <br />
                  </>
                ) : (
                  <>
                    <strong style={{ color: 'red' }}>"예술/디자인"</strong>{' '}
                    카드에 필요한 코인이 모두 모였어요! <br />
                    전략카드 구매 버튼을 눌러 카드를 구매해봅시다!
                  </>
                )
              ) : isBuy === 'Card' ? (
                isTPhase === 2 ? (
                  <>
                    전략카드 구매 창입니다. <br />
                    구매 가능한 <br />
                    <strong style={{ color: 'red' }}>"IT/과학"</strong> 카드
                    선택하여 게임을 끝내보세요!
                  </>
                ) : (
                  <>
                    전략카드 구매 창입니다. <br />
                    구매 가능한 <br />
                    <strong style={{ color: 'red' }}>"예술/디자인"</strong> 카드
                    선택하세요!
                  </>
                )
              ) : (
                ''
              )}
            </div>
          </div>
        </div>

        <div className="Log">
          <div className="Log_text">
            {isTPhase === 2 ? (
              <>- {isUserName}님이 직업윤리 코인의 능력을 사용하였습니다.</>
            ) : (
              ''
            )}
          </div>
        </div>

        {isBuy === '' ? (
          <div className="Coinbox">
            {isTCoin.map((data) => {
              return (
                <div className="CoinContent">
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-evenly',
                      marginTop: '10px',
                    }}
                  >
                    <button
                      className="CoinAddButton"
                      onClick={() => show(data.name)}
                    >
                      ?
                    </button>
                  </div>

                  <div className="CoinImg">{showCoin(data.name)}</div>
                  <div
                    className="CoinText"
                    style={{ color: displayColor(data.name) }}
                  >
                    {data.name}
                  </div>
                  <div className="CoinNum">{data.num}</div>
                </div>
              );
            })}
            <div className="ControllBox">
              <button
                className="ControllButton"
                onClick={() => {
                  Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: '카드를 사야만 해요!',
                  });
                }}
              >
                코인 구매
              </button>

              {!isTAct ? (
                <button
                  className="ControllButtonT"
                  onClick={() => setBuy('Card')}
                >
                  직업카드 구매
                </button>
              ) : (
                <button
                  className="ControllButton"
                  onClick={() => setBuy('Card')}
                >
                  직업카드 구매
                </button>
              )}
            </div>
          </div>
        ) : isBuy === 'Card' ? (
          <div className="Coinbox">
            {isTCoin.map((data) => {
              return (
                <div className="CoinContent">
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-evenly',
                      marginTop: '10px',
                    }}
                  >
                    <button
                      className="CoinAddButton"
                      onClick={() => show(data.name)}
                    >
                      ?
                    </button>
                  </div>

                  <div className="CoinImg">{showCoin(data.name)}</div>
                  <div
                    className="CoinText"
                    style={{ color: displayColor(data.name) }}
                  >
                    {data.name}
                  </div>
                  <div className="CoinNum">{data.num}</div>
                </div>
              );
            })}
            <div className="ControllBox">
              <button className="ControllButton" onClick={() => setBuy('')}>
                뒤로 가기
              </button>
            </div>
          </div>
        ) : (
          ''
        )}
      </>
    );
  };
  const StatePhase = () => {
    return (
      <>
        <div style={{ display: 'inline-flex', paddingTop: '30px' }}>
          <div className="Cardbox">{displaycard()}</div>
          <div className="Alert">
            <div className="Round">Tutorial Mode</div>
            <div className="AlertContent">
              라운드 결과창입니다. <br />
              팀원들이 구매한 카드를 확인 할 수 있어요
              <br />
              직업카드를
              {isTPhase === 1 ? (
                <>
                  <label className="Result"> 9</label> 장 모았어요!
                </>
              ) : (
                <>
                  <label className="Result"> 10</label> 장 모았어요!
                </>
              )}
            </div>
          </div>
        </div>

        <div className="Log">
          <div className="Log_text"></div>
        </div>

        <div className="Coinbox">
          {isTCoin.map((data) => {
            return (
              <div className="CoinContent">
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    marginTop: '10px',
                  }}
                >
                  <button
                    className="CoinAddButton"
                    onClick={() => takeCoin(data.name, data.num, '+')}
                  >
                    +
                  </button>
                  <button
                    className="CoinAddButton"
                    onClick={() => show(data.name)}
                  >
                    ?
                  </button>
                </div>

                <div className="CoinImg">{showCoin(data.name)}</div>
                <div
                  className="CoinText"
                  style={{ color: displayColor(data.name) }}
                >
                  {data.name}
                </div>
                <div className="CoinNum">{data.num}</div>
              </div>
            );
          })}
          <div className="ControllBox">
            <button
              className="ControllButtonT"
              onClick={() => EndTurn('End_StateTurn')}
            >
              다음 라운드
            </button>
          </div>
        </div>
      </>
    );
  };
  return (
    <>
      {!isTEndingTurn ? (
        <div className="Background">
          {isTFirstTurn && FirstTurn()}
          {isTAvailtyTurn && AvailtyPhase()}
          {isTBuyTurn && BuyPhase()}
          {isTStateTurn && StatePhase()}
        </div>
      ) : (
        <div className="Ending">
          <div className="titles">
            <p className="typing-txt" style={{ marginTop: '250px' }}>
              튜토리얼을 모두 마쳤습니다! 본 게임에서 빠르게 끝내 높은 점수를
              달성해보아요!
            </p>
          </div>
          <div style={{ display: 'flex', marginTop: '10%', height: '10%' }}>
            <button className="ControllButton" onClick={() => history('/')}>
              끝내기
            </button>
          </div>
        </div>
      )}
    </>
  );
}
