import gameContext from '../../../gameContext';
import { useContext } from 'react';
import { goodValueContext, badValueContext } from '../../../gameContext';
import '../Toolkit.css';
export function Toolkit() {
  const {
    isToolkitArray,
    displayToolkit,
    isRoomName,
    VillagProblem,
    isWin,
    textarea1,
    textarea2,
    textarea3,
    isScore,
    allUserName,
  } = useContext(gameContext);

  let d: any[] = [];
  let d2: any[] = [];
  let t1: any[] = [];
  let t2: any[] = [];
  let t3: any[] = [];

  // 마을 특징 출력 함수
  const displayD = () => {
    for (let i = 0; i < isToolkitArray.length; i++) {
      for (let j = 0; j < displayToolkit.length; j++) {
        console.log(i === displayToolkit[j]);
        if (i === displayToolkit[j]) {
          d.push(
            <div className="ToolkitpageGridbtn" key={i}>
              {goodValueContext[i]}
            </div>
          );
        }
      }
    }
    return d;
  };

  const displayD2 = () => {
    for (let i = 0; i < isToolkitArray.length; i++) {
      for (let j = 0; j < displayToolkit.length; j++) {
        console.log(i === displayToolkit[j]);
        if (i === displayToolkit[j]) {
          d2.push(
            <div className="ToolkitpageGridbtnR" key={i}>
              {badValueContext[i]}
            </div>
          );
        }
      }
    }
    return d2;
  };

  // 사후 툴킷 내용 출력 함수
  const display = (array: Array<string>) => {
    if (array.length === 0) return;
    else {
      switch (array) {
        case textarea1:
          for (var i = 0; i < array.length; i++) {
            t1.push(
              <>
                {array[i]}
                <br />
              </>
            );
          }
          return t1;
        case textarea2:
          for (var j = 0; j < array.length; j++) {
            t2.push(
              <>
                {array[j]}
                <br />
              </>
            );
          }
          return t2;
        case textarea3:
          for (var k = 0; k < array.length; k++) {
            t3.push(
              <>
                {array[k]}
                <br />
              </>
            );
          }
          return t3;
        default:
          break;
      }
    }
  };

  return (
    <div className="Toolkitfinishpage">
      <div className="ToolkitfinishTitle" style={{ margin: '20px auto' }}>
        우리가 마을문제를 해결하기 위한 노력들
      </div>
      <div className="ToolkitfinishpageText">
        조 이름 :<div className="ToolkitfinishpageInput">{isRoomName}</div>
      </div>
      <div className="ToolkitfinishpageText">
        조 원 :
        <div className="ToolkitfinishpageInput">
          {allUserName.map((item: any) => {
            return ' ' + item + '';
          })}
        </div>
      </div>

      <div className="Toolkitfinishpageline" />

      <div className="ToolkitfinishpageText">마 을 특 징 :</div>
      <div className="ToolkitpageGridS">
        {displayD()}
        {displayD2()}
      </div>

      <div className="ToolkitfinishpageText">
        해결 할 마을 문제 :
        <div className="ToolkitfinishpageInput">{VillagProblem}</div>
      </div>

      <div className="ToolkitfinishpageText">문제해결 방법</div>
      <div className="ToolkitfinishpageTextArea"> {display(textarea1)}</div>
      <div className="ToolkitpageGridCardCheck">
        <div className="ToolkitpageGridCardCheckbtn0">
          <div className="ToolkitpageGridCardChecklabel">{isWin[0]}</div>
        </div>

        <div className="ToolkitpageGridCardCheckbtn1">
          <div className="ToolkitpageGridCardChecklabel">{isWin[1]}</div>
        </div>

        <div className="ToolkitpageGridCardCheckbtn2">
          <div className="ToolkitpageGridCardChecklabel">{isWin[2]}</div>
        </div>

        <div className="ToolkitpageGridCardCheckbtn3">
          <div className="ToolkitpageGridCardChecklabel">{isWin[3]}</div>
        </div>

        <div className="ToolkitpageGridCardCheckbtn4">
          <div className="ToolkitpageGridCardChecklabel">{isWin[4]}</div>
        </div>

        <div className="ToolkitpageGridCardCheckbtn5">
          <div className="ToolkitpageGridCardChecklabel">{isWin[5]}</div>
        </div>
      </div>

      <div className="ToolkitfinishpageText">
        게 임 점 수 :
        <div className="ToolkitfinishpageInput">{isScore + '점'}</div>
      </div>

      <div className="Toolkitfinishpageline" />
      <div className="ToolkitfinishpageGrid">
        <div>
          <div className="ToolkitfinishpageText">
            Q. 플랜빌리지 게임 진행 시, 우리팀의 전략은?
          </div>
          <div className="ToolkitfinishpageTextArea1">{display(textarea2)}</div>
        </div>
        <div>
          <div className="ToolkitfinishpageText">
            Q. 게임진행 시, 어려웠던 점은 무엇이었나요?
          </div>
          <div className="ToolkitfinishpageTextArea1">{display(textarea3)}</div>
        </div>
      </div>
    </div>
  );
}
