export const disasterToCoin = (input: number) => {
  switch (input) {
    case 1:
      return '리더십';
    case 2:
      return '창의력';
    case 3:
      return '문제해결';
    case 4:
      return '분석력';
    case 5:
      return '의사소통';
    case 6:
      return '직업윤리';
    default:
      return '몰?루';
  }
};

