export const disasterType = (input: number, input2: number) => {
  switch (input) {
    case 1:
      return Swal.fire({
        icon: 'warning',
        title: '권위적 간섭',
        html: `마을을 위해서는 가끔씩 권위적인 힘이 필요할 떄가 있는 법이죠.<br/> 
          "${disasterToCoin(input2)}" 코인을 가져올 수 없습니다.<br/>
          (리더십 코인으로 막을 수 있어요!)
          `,
      });
    case 2:
      return Swal.fire({
        icon: 'warning',
        title: '시민의식 저하',
        html: `마을을 재건하기 위한 강행군이 시민들의 능력을 저하시켰습니다. <br/>
"          ${disasterToCoin(input2)}" 코인을 전부 회수합니다.<br/>
          (직업윤리 코인으로 막을 수 있어요!)`,
      });
    case 3:
      return Swal.fire({
        icon: 'warning',
        title: '의욕 저하',
        html: `사람들이 과도한 재건활동에 지쳐갑니다. <br/>
              가져올 수 있는 코인의 개수가 1개로 감소합니다.`,
      });
    case 4:
      return Swal.fire({
        icon: 'warning',
        title: '구인/구직의 어려움',
        html: `취업하기 어려운 세상이에요<br/> 모두 고스펙 고학벌을 원합니다.<br/>
                직업 카드를 구매할 때 종류당 하나씩 더 코인이 필요합니다.`,
      });
    case 5:
      return Swal.fire({
        icon: 'warning',
        title: '인생의 쓴맛',
        html: `世上萬事 塞翁之馬. <br/>인생사가 다 그런 것이니 너무 슬퍼하지 말거라.<br/>
                  갖고있는 직업카드 중 랜덤으로 1장을 잃습니다.<br/>
                  (문제해결 코인으로 막을 수 있어요!)`,
      });
    default:
      return;
  }
};

