export const show = (data: string) => {
  const coinAvailty = [
    {
      name: '리더십',
      num: 3,
      availtyname: '민주적 해결',
      availty: '‘권위적 간섭’의 효과(무작위 한 종류 코인 구매불가)를 막습니다.',
    },
    {
      name: '창의력',
      num: 3,
      availtyname: '선한 영향력',
      availty: '이번 라운드에는 코인을 1개씩 추가로 얻습니다.',
    },
    {
      name: '문제해결',
      num: 3,
      availtyname: '달콤한 인생에 박수를',
      availty:
        '‘인생의 쓴맛’의 효과(갖고있는 직업카드 중 랜덤으로 1장 회수)를 막습니다.',
    },
    {
      name: '분석력',
      num: 1,
      availtyname: '지식 교환',
      availty: '원하는 코인 1종류를 다른 종류의 코인으로 교환합니다.',
    },
    {
      name: '의사소통',
      num: 3,
      availtyname: '토론의 장',
      availty: "'1분'동안 팀과 의논 할 수 있는 시간을 갖습니다.",
    },
    {
      name: '직업윤리',
      num: 3,
      availtyname: '모범시민',
      availty:
        '시민의식 저하’의 효과(무작위 한 종류 코인을 전부 회수)를 막습니다.',
    },
  ];

  coinAvailty.map(function (item) {
    if (item.name === data) {
      return Swal.fire({
        toast: true,
        title: data,
        html: ` 
          <h2> ${item.availtyname}</h2>
          필요한 코인 갯수 : ${item.num} <br/>
            능 력 : ${item.availty}`,
        width: 500,
        showConfirmButton: true,
        confirmButtonText: '확인',
        background: '#fff',
      });
    }
    return '';
  });
};
