export const impactavailty = (data: string) => {
  switch (data) {
    case '리더십':
      return Swal.fire({
        icon: 'success',
        title: '민주적 해결',
        html: `민주적인 토론과 대화는 갈등을 해결하는 좋은 수단이죠.<br/>
        (권위적 간섭’의 효과(무작위 한 종류 코인을 구매불가)를 막았습니다!)`,
      });
    case '창의력':
      return Swal.fire({
        icon: 'success',
        title: '선한 영향력',
        html: `누군가의 헌신적인 행동은 세상을 바꾸는 첫 걸음이랍니다. <br/>
                (이번 라운드에는 코인을 1개씩 추가로 얻습니다.)`,
      });
    case '문제해결':
      return Swal.fire({
        icon: 'success',
        title: '달콤한 인생에 박수를',
        html: `인생에 내리막이 있으면 오르막도 있는 법<br/>
        ‘인생의 쓴맛’의 효과(갖고있는 직업카드 중 랜덤으로 1장 회수)를 막았습니다!.`,
      });
    case '분석력':
      return Swal.fire({
        icon: 'success',
        title: '지식 교환',
        html: `변화와 혁신을 통한 개혁<br/>
        (원하는 코인 1종류를 다른 종류의 코인으로 교환합니다.)`,
      });
    case '의사소통':
      return Swal.fire({
        icon: 'success',
        title: '토론의 장',
        html: `1분간의 대화, 소통의 중요성을 느끼기에 충분한 시간입니다.<br/>
        ('1분'동안 팀과 의논 할 수 있는 시간을 갖습니다.)`,
      });
    case '직업윤리':
      return Swal.fire({
        icon: 'success',
        title: '모범시민',
        html: `모범적인 삶, 돌아오는 큰 보상 <br/>
        시민의식 저하’의 효과(무작위 한 종류 코인을 전부 회수)를 막았습니다!.`,
      });
    default:
      break;
  }
};
