import gameContext from '../../../gameContext';
import { useContext, useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import socketService from '../../../services/socketService';
import toolkitService from '../../../services/toolkitService';
import Swal from 'sweetalert2';
import {
  goodValueContext,
  badValueContext,
  VillageValue,
  VillageProblems,
} from '../../../gameContext';
import '../Toolkit.css';
export interface IStartToolkit {
  enter: number;
  protagonist: string;
  phase: number;
  villageProblem: string;
  goodValue: number[];
  occupation: string[];
  text1: string[];
  win: number[];
}

export function Toolkitbefore() {
  const {
    isUserNum,
    isUserName,
    isRoomName,
    isToolkitArray,
    setToolkitArray,
    isToolkitlist,
    setToolkitlist,
    VillagProblem,
    setVillageProblem,
    isToolkitphase,
    setToolkitphase,
    isProtagonist,
    setProtagonist,
  } = useContext(gameContext);

  const [rendering, setRendring] = useState<boolean>(true);
  const [isToolkitArrayR, setToolkitArrayR] = useState<Array<number>>([]);
  const history = useNavigate();
  const [str, setStr] = useState('');
  const [Enter, setEnter] = useState<number>(0);
  const isResult1 = useRef<number>(0);
  const isResult2 = useRef<number>(0);
  const isResult3 = useRef<number>(0);
  const isResult4 = useRef<number>(0);

  let t: any[] = [];
  // 사전 툴킷 입장
  const handleToolkitEnter = () => {
    if (socketService.socket)
      toolkitService.EnterToolkit(socketService.socket, async (option) => {
        setEnter(option.enter);
      });
  };

  // 사전 툴킷 첫 접속 & 재접속시 정보 받아오기
  const handleToolkitStart = () => {
    if (socketService.socket) {
      toolkitService.onStartToolkit(socketService.socket, async (options) => {
        // protagonist 설정
        if (options.protagonist === isUserName && options.phase === 0) {
          setProtagonist(true);
          if (socketService.socket) {
            toolkitService.updateStartToolkit(socketService.socket, {
              id: isRoomName,
              phase: 1,
            });
          }
        }
        setToolkitphase(options.phase);
        if (options.goodValue.length !== 0) {
          let a: any = Array<string>(9);
          for (let i = 0; i < options.goodValue.length; i++) {
            a[i] = options.goodValue[i];
            switch (VillageValue[options.goodValue[i]]) {
              case 1:
                isResult1.current += 1;
                break;
              case 2:
                isResult2.current += 1;
                break;
              case 3:
                isResult3.current += 1;
                break;
              case 4:
                isResult4.current += 1;
                break;
              default:
                break;
            }
          }
          setToolkitArrayR(a);
        }
        setToolkitlist(options.occupation);
        setVillageProblem(options.villageProblem);
      });
    }
  };

  // 사전 툴킷 phase 변화를 위해 서버에 요청 하는 함수
  const phaseChange = () => {
    if (isProtagonist === true) {
      if (socketService.socket) {
        switch (isToolkitphase) {
          case 1:
            let indexArray: Array<number> = [];
            for (let i = 0; i < 27; i++) {
              if (isToolkitArray[i] === true) {
                indexArray = indexArray.concat(i);
              }
              if (i === 26) {
                const Arr: Array<any> = [
                  isResult1.current,
                  isResult2.current,
                  isResult3.current,
                  isResult4.current,
                ];

                let max = Arr[0];
                let index = 0;
                for (let i = 0; i < Arr.length; i++) {
                  if (Arr[i] > max) {
                    max = Arr[i];
                    index = i;
                    console.log(index);
                  }
                }
                setToolkitArrayR(indexArray);
                setVillageProblem(VillageProblems[index]);
                toolkitService.phaseChange(socketService.socket, {
                  phase: isToolkitphase + 1,
                  goodValue: indexArray,
                  villageProblem: VillageProblems[index],
                  id: isRoomName,
                });
              }
            }
            break;
          case 2:
            toolkitService.phaseChange(socketService.socket, {
              phase: isToolkitphase + 1,
              id: isRoomName,
            });
            break;
          default:
            break;
        }
      }
    }
  };

  const selectContent = (i: number, check: boolean) => {
    if (isProtagonist === true) {
      if (socketService.socket) {
        toolkitService.selectContent(
          socketService.socket,
          isToolkitArray,
          i,
          check
        );
      }
    }
  };

  // 사전 툴킷 phase 1 활성화된 마을 특징 갯수 세기 (6개)
  const countActive = (array: Array<any>) => {
    return array.filter((array) => array === true).length;
  };

  // Onchange 함수
  const handleOnchange = (e: React.ChangeEvent<any>) => {
    setRendring(false);
    const value = e.target.value;
    switch (e.target.id) {
      case 'isToolkitlist':
        setStr(value);
        break;
      default:
        break;
    }
  };

  // 사전 툴킷 phase 2 마을 정보 서버에 보내기
  const onChangeWrite = (id: string, contents: string) => {
    if (isProtagonist) {
      if (socketService.socket) {
        toolkitService.onChangeWrite(socketService.socket, id, contents);
        setRendring(true);
        setStr('');
      }
    }
  };

  // 사전 툴킷 종료 함수
  const finishToolkit = () => {
    if (socketService.socket)
      toolkitService.finishToolkit(socketService.socket, {
        roomName: isRoomName,
        userName: isUserName,
        before: true,
        list: isToolkitlist,
      });
    history('/');
  };

  // 툴킷 시작 & 툴킷 정보 & phase 변화를 서버에서 받아오는 함수
  useEffect(() => {
    function change() {
      if (socketService.socket) {
        toolkitService.phaseChanger(socketService.socket, (phase) => {
          if (socketService.socket) {
            setToolkitphase(phase);
            if (phase === 2) {
              let indexArray: Array<number> = [];
              for (let i = 0; i < 27; i++) {
                if (isToolkitArray[i] === true) {
                  indexArray = indexArray.concat(i);
                }
              }
              const Arr: Array<any> = [
                isResult1.current,
                isResult2.current,
                isResult3.current,
                isResult4.current,
              ];
              let max = Arr[0];
              let index = 0;
              for (let i = 0; i < Arr.length; i++) {
                if (Arr[i] > max) {
                  max = Arr[i];
                  index = i;
                }
              }
              setToolkitArrayR(indexArray);
              setVillageProblem(VillageProblems[index]);
            }
            console.log('next Phase');
          }
        });
      }
    }
    function check() {
      switch (isToolkitphase) {
        case 1:
          if (socketService.socket) {
            toolkitService.updateContent(socketService.socket, (data) => {
              let newArray = data.contents;
              if (data.check) {
                newArray[data.number] = true;
                switch (VillageValue[data.number]) {
                  case 1:
                    isResult1.current += 1;
                    break;
                  case 2:
                    isResult2.current += 1;
                    break;
                  case 3:
                    isResult3.current += 1;
                    break;
                  case 4:
                    isResult4.current += 1;
                    break;
                  default:
                    break;
                }
              } else {
                newArray[data.number] = false;
                switch (VillageValue[data.number]) {
                  case 1:
                    isResult1.current -= 1;
                    break;
                  case 2:
                    isResult2.current -= 1;
                    break;
                  case 3:
                    isResult3.current -= 1;
                    break;
                  case 4:
                    isResult4.current -= 1;
                    break;
                  default:
                    break;
                }
              }
              setToolkitArray(newArray);
            });
          }
          break;
        case 3:
          if (socketService.socket) {
            toolkitService.updateWrite(socketService.socket, (id, contents) => {
              switch (id) {
                case 'isToolkitlist':
                  setToolkitlist(isToolkitlist.concat(contents));
                  break;
                default:
                  break;
              }
            });
          }
          break;
        default:
          break;
      }
    }
    if (rendering) {
      change();
      check();
    }
    if (Enter < parseInt(window.sessionStorage.num)) {
      handleToolkitEnter();
      handleToolkitStart();
    }
  });

  // 마을 특징 고르기 제어 함수
  function UpdateContent({
    choice,
    txt,
    index,
  }: {
    choice: boolean;
    txt: any;
    index: number;
  }) {
    if (choice) {
      return (
        <div
          className="ToolkitpageGridbtnA"
          key={index}
          onClick={() => selectContent(index, false)}
        >
          {txt}
        </div>
      );
    } else {
      return (
        <div
          className="ToolkitpageGridbtn"
          key={index}
          onClick={() => selectContent(index, true)}
        >
          {txt}
        </div>
      );
    }
  }

  const popupdisplay = (i: number) => {
    switch (i) {
      case 0:
        return Swal.fire({
          title: '예술디자인',
          html: `
          디자이너 (시각,광고,인테리어,제품,패션), 화가,웹 프로듀서,광고기획자,출판편집자, 작가`,
        });
      case 1:
        return Swal.fire({
          title: '사회/공공기관',
          html: `
          국회의원,공무원,경찰관,소방관,검사,변호사,교사
          `,
        });
      case 2:
        return Swal.fire({
          title: '과학/IT/공학',
          html: `
          드론전문가,개발자,소프트웨어개발자,의료정보시스템개발자,건축사,공학기술자 (화학,기계,통신,토목,지리)
          `,
        });
      case 3:
        return Swal.fire({
          title: '의료/보건',
          html: `
          의사,간호사,임상병리사,위생사,약사,영양사
          `,
        });
      case 4:
        return Swal.fire({
          title: '서비스/봉사',
          html: `
          판매원,음식점 사장님,사회복지사,어린이집 선생님,상담사
          `,
        });
      case 5:
        return Swal.fire({
          title: '자연친화',
          html: `토양환경기술자,환경공학기술자,농업기술자,원예기술자
          `,
        });

      default:
        break;
    }
  };

  // const popupdisplay = (i: number) => {
  //   switch (i) {
  //     case 0:
  //       return Swal.fire({
  //         title: '신제품 개발',
  //         html: `
  //         1. 분리수거를 귀찮아 하는 사람들, 쓰레기 무단투기를 방지하기 위해 자체적으로 재활용이 되는 쓰레기통 개발 <br/>
  //         2. 코로나 19로 인한 어린이의 안전이 염려된다! 어린이 전용 그네에 자동으로 소독되는 자동소독장치 개발!  <br/>
  //         `,
  //       });
  //     case 1:
  //       return Swal.fire({
  //         title: '지자체 공공기관',
  //         html: `
  //         1. 나도 저거 하고 싶다! 구청, 시청에서 청소년 사회참여 프로그램을 만들어달라! 청소년들의 참여활동 증가!<br/>
  //         2. 한 번쓰고 버리지말고 아껴쓰고 나눠쓰고 바꿔쓰고 다시 쓰자! 마을축제를 열어 아나바다를 열어보자. <br/>
  //         `,
  //       });
  //     case 2:
  //       return Swal.fire({
  //         title: '공유플랫폼',
  //         html: `
  //         1. 전동킥보드 주차문제로 청소년들이 모여 캠페인을 열고자 한다! 전국 청소년 오픈채팅방을 개설하여 캠패인 인원 모집.  <br/>
  //         2. 우리동네 안전구역을 찾아 SNS숏츠 영상 제작을 통해 범죄예방 챌린지 진행!  <br/>

  //         `,
  //       });
  //     case 3:
  //       return Swal.fire({
  //         title: '공동체/인적자원',
  //         html: `
  //         1. 우리 할머니 글자를 알려드리고 싶은데… 마을공동체 활성화를 통한 사회적 약자를 위한 교육 진행. <br/>
  //         2. 쓰레기가 너무 많이 버려지고 있어, 함께 할 사람들을 모아 공동체로 줍깅 프로젝트를 활성화 시켜보자. <br/>
  //         `,
  //       });
  //     case 4:
  //       return Swal.fire({
  //         title: '디자인',
  //         html: `
  //         1. 시각장애가 있는 분들이 음식점에서 음식을 시켜먹기 어려워 한다고?! 시각장애를 위한 점자 메뉴판을 디자인해보자. <br/>
  //         2. 바닥 대신에 담배곽에 꽁초를 버릴 수 있는 담배곽을 디자인 해볼까? <br/>
  //         `,
  //       });
  //     case 5:
  //       return Swal.fire({
  //         title: '지역자원활용',
  //         html: `
  //         1. 몇 년동안 아무도 안쓰는 공간, 버려진 유휴공간을 활용하여 공유 주방으로! 마을 사람들이 모여 함께 요리 해보자. <br/>
  //         2. 먼지가 날리는 공사판이 아닌, 역사를 배울 수 있는 공사판 외벽을 활용한 마을 역사 전시를 열어보자.  <br/>

  //         `,
  //       });

  //     default:
  //       break;
  //   }
  // };

  const display = (array: Array<string>) => {
    if (array.length === 0) return;
    else {
      for (var i = 0; i < array.length; i++) {
        t.push(
          <>
            {array[i]}
            <br />
          </>
        );
      }
      return t;
    }
  };

  return (
    <div className="Toolkitpage">
      {Enter === parseInt(window.sessionStorage.num) ? (
        isToolkitphase === 1 ? (
          <>
            <div className="Toolkittitle">
              우리 마을의 특징 골라보기
              {countActive(isToolkitArray) === 9 ? (
                <div
                  className="Toolkitpagebtn"
                  style={{ position: 'absolute', left: '1100px' }}
                  onClick={() => phaseChange()}
                />
              ) : (
                ''
              )}
            </div>

            <div className="ToolkitpageGrid">
              {isToolkitArray.map((item, index) => (
                <UpdateContent
                  choice={item}
                  txt={goodValueContext[index]}
                  index={index}
                  key={index}
                ></UpdateContent>
              ))}
            </div>
          </>
        ) : isToolkitphase === 2 ? (
          <>
            <div className="Toolkittitle">
              우리 마을 문제 해결하기
              <div
                className="Toolkitpagebtn"
                style={{ position: 'absolute', left: '1200px' }}
                onClick={() => phaseChange()}
              />
            </div>
            <div className="ToolkitpageText">마을특징 :</div>
            <div className="ToolkitpageGridS">
              {isToolkitArrayR.map((item, index) => {
                return (
                  <div className="ToolkitpageGridbtn" key={index}>
                    {goodValueContext[String(item)]}
                  </div>
                );
              })}
              {isToolkitArrayR.map((item, index) => {
                return (
                  <div className="ToolkitpageGridbtnR" key={index}>
                    {badValueContext[String(item)]}
                  </div>
                );
              })}
            </div>
            <div className="ToolkitpageText">
              발생 가능한 마을 문제 :
              <div
                style={{
                  color: '#005CDC',
                  fontFamily: 'SB',
                  marginLeft: '0.5%',
                }}
              >
                {VillagProblem}
              </div>
            </div>
            <div className="ToolkitpageResult">
              <div>
                <div className="ToolkitpageText2">
                  {VillageProblems[0]} :
                  <div
                    style={{
                      color: '#005CDC',
                      fontFamily: 'SB',
                      marginLeft: '0.5%',
                    }}
                  >
                    {Math.round(
                      (isResult1.current /
                        (isResult1.current +
                          isResult2.current +
                          isResult3.current +
                          isResult4.current)) *
                        1000
                    ) / 10}
                    %
                  </div>
                </div>
                <div className="ToolkitpageText2">
                  {VillageProblems[1]}:
                  <div
                    style={{
                      color: '#005CDC',
                      fontFamily: 'SB',
                      marginLeft: '0.5%',
                    }}
                  >
                    {Math.round(
                      (isResult2.current /
                        (isResult1.current +
                          isResult2.current +
                          isResult3.current +
                          isResult4.current)) *
                        1000
                    ) / 10}
                    %
                  </div>
                </div>
              </div>
              <div>
                <div className="ToolkitpageText2">
                  {VillageProblems[2]} :
                  <div
                    style={{
                      color: '#005CDC',
                      fontFamily: 'SB',
                      marginLeft: '0.5%',
                    }}
                  >
                    {Math.round(
                      (isResult3.current /
                        (isResult1.current +
                          isResult2.current +
                          isResult3.current +
                          isResult4.current)) *
                        1000
                    ) / 10}
                    %
                  </div>
                </div>
                <div className="ToolkitpageText2">
                  {VillageProblems[3]}:
                  <div
                    style={{
                      color: '#005CDC',
                      fontFamily: 'SB',
                      marginLeft: '0.5%',
                    }}
                  >
                    {Math.round(
                      (isResult4.current /
                        (isResult1.current +
                          isResult2.current +
                          isResult3.current +
                          isResult4.current)) *
                        1000
                    ) / 10}
                    %
                  </div>
                </div>
              </div>
            </div>
          </>
        ) : isToolkitphase === 3 ? (
          <>
            <div
              className="ToolkitpageText"
              style={{ padding: '40px 0 20px 40px' }}
            >
              문제해결 방법
              <input
                className="ToolkitpageInput"
                id="isToolkitlist"
                value={str}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="Toolkitpagebtn"
                style={{ margin: '0 20px' }}
                onClick={() => onChangeWrite('isToolkitlist', str)}
              />
            </div>
            <div className="ToolkitpageTextArea">{display(isToolkitlist)}</div>
            <div
              className="ToolkitpageText"
              style={{ padding: '40px 0 0 40px' }}
            >
              문제해결을 위한 직업 :
            </div>
            <div className="ToolkitpageGridCardCheck">
              <div className="ToolkitpageGridCardCheckbtn0">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(0)}
                >
                  직업예시
                </div>
              </div>

              <div className="ToolkitpageGridCardCheckbtn1">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(1)}
                >
                  직업예시
                </div>
              </div>

              <div className="ToolkitpageGridCardCheckbtn2">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(2)}
                >
                  직업예시
                </div>
              </div>

              <div className="ToolkitpageGridCardCheckbtn3">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(3)}
                >
                  직업예시
                </div>
              </div>

              <div className="ToolkitpageGridCardCheckbtn4">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(4)}
                >
                  직업예시
                </div>
              </div>

              <div className="ToolkitpageGridCardCheckbtn5">
                <div
                  className="ToolkitpageGridCardChecklabel"
                  onClick={() => popupdisplay(5)}
                >
                  직업예시
                </div>
              </div>
            </div>

            {isToolkitlist.length > 5 && (
              <div
                className="Toolkitpagehomebtn"
                onClick={() => finishToolkit()}
              />
            )}
          </>
        ) : (
          <></>
        )
      ) : (
        <>
          <div className="Wait_Logo_P" />
          <div className="Wait_Logo_L" />
          <div className="Wait_Logo_A" />
          <div className="Wait_Logo_N" />
          <div className="Wait_Logo_V" />
          <div className="Wait_Logo_I" />
          <div className="Wait_Logo_L2" />
          <div className="Wait_Logo_L3" />
          <div className="Wait_Logo_A2" />
          <div className="Wait_Logo_G" />
          <div className="Wait_Logo_E" />
          <div className="Wait_User">
            {isRoomName === '' ? (
              <div className="reStartbutton" onClick={() => history('/')}>
                재접속
              </div>
            ) : (
              <>
                방 제목: {isRoomName} <br />
              </>
            )}
            <br />
            입장인원:{' '}
            {Enter !== 0
              ? `${Enter}/${isUserNum}`
              : `${isUserNum}/${isUserNum}`}
          </div>
        </>
      )}
    </div>
  );
}
